import React, { Component } from 'react';
import Aux from '../../hoc/Aux';
import { loadModels, getFullFaceDescription, createMatcher } from '../../api/face';
import JSON_PROFILE from '../../descriptors/bnk48.json';

class FaceRecognize extends Component {
    state = {
        imageURL: process.env.PUBLIC_URL + 'images/test.jpeg',
        fullDesc: null,
        detections: null,
        descriptors: null,
        match: null
    };

    componentWillMount = async () => {
        await loadModels();
        this.setState({ faceMatcher: await createMatcher(JSON_PROFILE) });
        await this.handleImage(this.state.imageURL);
    };

    handleImage = async (image = this.state.imageURL) => {
        await getFullFaceDescription(image).then(fullDesc => {
            console.log(fullDesc);
            this.setState({
                fullDesc,
                detections: fullDesc.map(fd => fd.detection),
                descriptors: fullDesc.map(fd => fd.descriptor)
            });
        });

        if (!!this.state.descriptors && !!this.state.faceMatcher) {
            let match = await this.state.descriptors.map(descriptor =>
                this.state.faceMatcher.findBestMatch(descriptor)
            );
            this.setState({ match });
        }
    };


    handleFileChange = async event => {
        this.resetState();
        await this.setState({
            imageURL: URL.createObjectURL(event.target.files[0]),
            loading: true
        });
        this.handleImage();
    };

    resetState = () => {
        this.setState({ ...this.state });
    };

    render() {
        const { imageURL, detections, match } = this.state;

        let drawBox = null;
        if (!!detections) {
            drawBox = detections.map((detection, i) => {
                let _H = detection.box.height;
                let _W = detection.box.width;
                let _X = detection.box._x;
                let _Y = detection.box._y;
                return (<div key={i}
                    style={{
                        position: 'absolute',
                        border: 'solid',
                        borderColor: 'blue',
                        height: _H,
                        width: _W,
                        transform: `translate(${_X}px,${_Y}px)`
                    }}>
                    {!!match && !!match[i] ? (
                        <p
                            style={{
                                backgroundColor: 'blue',
                                border: 'solid',
                                borderColor: 'blue',
                                width: _W,
                                marginTop: 0,
                                color: '#fff',
                                transform: `translate(-3px,${_H}px)`
                            }}
                        >
                            {match[i]._label}
                        </p>
                    ) : null}

                </div>
                );
            });
        }


        return (
            <Aux>
                {/* <img src={imageURL} alt="imageURL" /> */}
                {/* <input
                    id="myFileUpload"
                    type="file"
                    onChange={this.handleFileChange}
                    accept=".jpg, .jpeg, .png"
                /> */}
                <form className=" mt-0">
                    <div className="form-group">
                        {/* <label htmlFor="exampleInputEmail1">Email address</label> */}
                        <input type="file" name="email"
                            onChange={this.handleFileChange}
                            accept=".jpg, .jpeg, .png"
                            required />
                    </div>
                    <div style={{ position: 'relative' }}>
                        <div style={{ position: 'absolute' }}>
                            <img src={imageURL} alt="imageURL" />
                        </div>
                        {!!drawBox ? drawBox : null}
                    </div>
                </form>
            </Aux>
        )
    }
}

export default FaceRecognize;
