import * as actionTypes from '../Actions/types';

// const INITIALDATA ={
//     data: {}
// }

const INITIALDATA = {
    facemactch: {}
}

const CreateUserReducer = (state = INITIALDATA, action) => {
    switch (action.type) {
        case actionTypes.FACEMATCH:
            return {
                ...state,
                facemactch: action.payload
            }

        default:
            return state;
    }
}

export default CreateUserReducer;