import React, { Component } from 'react';
import { connect } from 'react-redux';
import Aux from '../../hoc/Aux';
import Camera from 'react-html5-camera-photo';
import 'react-html5-camera-photo/build/css/index.css';
import { FaceMatchAction } from '../../store/Actions/UserListAction';

export class FaceMatch extends Component {
    state = {
        imgPath: '',
        base64Img: ''
    }


    handleTakePhoto = (dataUri) => {
        const base64result = dataUri.split(',')[1];
        // console.log(base64result);

        this.setState({
            base64Img: base64result,
            imgPath: dataUri
        });
        this.props.FaceMatchAction(this.state.base64Img);
    }

    render() {
        const liveness = parseFloat(this.props.CreateUserReducer.facemactch?.liveness).toFixed(3);
        const respdesc = this.props.CreateUserReducer.facemactch?.respdesc;

        return (
            <Aux>
                <div className="row justify-content-center">
                    <div className="col-md-5">
                        <form className="form mt-4">
                            <Camera
                                onTakePhoto={(dataUri) => { this.handleTakePhoto(dataUri); }}
                                idealResolution={{ width: 1280, height: 720 }}
                            />
                        </form>
                    </div>
                    <div className="col-md-5">
                        <form className="form mt-4">
                            <h1 className="h3 mb-3">Output</h1>
                            <ul className="list-group">
                                <li className="list-group-item d-flex justify-content-between align-items-center">
                                    Response
                                    {respdesc === "success" ? <span className="badge badge-success badge-pill">{respdesc}</span>
                                        : <span className="badge badge-danger badge-pill">{respdesc}</span>}

                                </li>
                                <li className="list-group-item d-flex justify-content-between align-items-center">
                                    Liveness
                                        {liveness ? liveness <= 50 ?
                                        <span className="badge badge-danger badge-pill">{liveness} %</span>
                                        : <span className="badge badge-success badge-pill">{liveness} %</span>
                                        : null}

                                </li>
                            </ul>

                        </form>
                        <form className="form mt-4">
                            <img className="w-100" src={this.state.imgPath} alt="no img" />
                        </form>
                    </div>
                </div>


            </Aux>
        )
    }
}

const mapStateToProps = (state) => {
    const { CreateUserReducer } = state;
    return { CreateUserReducer };
}

const mapDispatchToProps = (dispatch) => {
    return {
        FaceMatchAction: (data) => dispatch(FaceMatchAction(data))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(FaceMatch);
