import * as actionTypes from './types';
import axios from '../../hoc/axios';


export const FaceMatchAction = (model) => {
    const url = "liveness";
    const body = {
        image: model
    }
    return (dispatch) => {
        axios.post(url, body)
            .then(res => {
                dispatch({
                    type: actionTypes.FACEMATCH,
                    payload: res.data
                })
            })
            .catch(
                err => {
                    console.log("No data or 404", err);
                }
            )
    }
}